    display: inline-block;
}
#home .tabs .tab .slider {
    background-color: white;
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
#home #open-the-sound {
    position: absolute;
    top: 20%;
    right: -3px;
    border: rgba(255, 255, 255, 0.5) solid 3px;
    z-index: 1002;
    overflow: hidden;
    display: block;
    font-size: 16px;
    background: none;
    width: 240px;
    padding: 15px;
    text-align: left;
    font-family: gothic,Arial,sans-serif;
    color: #fff;
    letter-spacing: 2px;
    /* text-transform: uppercase; */
    cursor: pointer;
    -webkit-transition: color .2s;
    transition: color .2s;
}

/* #home #open-the-sound:before, */
#home #open-the-sound:after {
  content: '';
  position: absolute;
  top: 0;
  width: 100%;
  height:100%;
  background-color: white;
/*   transition: all 0.2s cubic-bezier(0.15, 0.75, 0.49, 0.93); */
  -webkit-transform:scaleY(0);
          transform:scaleY(0);
  z-index: -1;
}
#home #open-the-sound:after {
  -webkit-transform-origin: top left;
          transform-origin: top left;
/*   bottom: 0; */
  right: 0;
}
#home #open-the-sound:hover {
  color:#ff0000;
  -webkit-transition: color .2s ;
  transition: color .2s ;
}
#home #open-the-sound:hover:after {
  -webkit-animation: view-reel-hover 0.5s cubic-bezier(0.15, 0.75, 0.49, 0.93) both;
          animation: view-reel-hover 0.5s cubic-bezier(0.15, 0.75, 0.49, 0.93) both;
}
